
  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    @media (min-width: 768px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    li {
       @media (min-width: 768px) {
        flex-basis: calc(33% - 15px);
        margin-bottom: 30px;
       }
    }
  }

  h2 {
    margin-bottom: 40px;
  }
