
  .intro {
    //background-image: url('~@/assets/Stars.png');
    min-height: 400px;
    padding: 120px 0 60px 0;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  img {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 110px;
  }
  .logo {
    position: relative;
    padding-bottom: 20px;
    max-width: 280px;
    line-height: 1.1;
    margin-bottom: 60px;

    .nft {
      font-style: italic;
      font-size: 20px;
      position: absolute;
      right: 0;
      bottom: 0;
      color: #b63700;
    }
  }

  p {
    text-align: center;
  };

